*,
*::after,
*::before{
   margin: 0;
   padding: 0;
   box-sizing: inherit;
}

html{
    font-size: 62.5%;

    @include respond(tab-land){
        font-size: 56.25%;                      //1rem = 9px , 9/16  56.25%        
      }
    
      @include respond(tab-port){
        font-size: 50%; 
                                        //1rem = 8px , 8/16  50%
        } 
    
        @include respond(phone){
          font-size: 30%;  
        }
      @include respond(big-desktop){
        font-size: 75%;                                //1rem = 12px 12/16  75%  
      }

}