body{
    font-family: "Evolventa", sans-serif;
}


.black-text{
    color: black;
}

.white-text{
    color: white;
}

.red-text{
    color: red;
}
.normal-text{
    font-size: 1.5rem;
}
.bigger-text{
    font-size: 2rem;
    @include respond(phone){
        font-size: 3rem;
    }
}

.bold-text{
    font-weight: bold;
}